body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container {
  background: #FFFFFF;
  overflow: hidden;
}

.Navbar {
  width: 100%;
  padding: 0.5rem 0;
  background-color: #a15d98;
}

.Navbar__brand {
  color: #ffffff;

  display: flex;
  align-items: center;
}

.Navbar__brand:hover {
  color: #ffffff;
  text-decoration: none;
}

.Navbar__brand-logo {
  margin-right: 0.5rem;
}

@media (prefers-reduced-motion: no-preference) {
  .app-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}
.App {
  background-color: #a15d98;
}

body{
  background-color: #a15d98;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header { 
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(15px + 4vmin);
}

.App-link {
  color: #ECC470;
  font-family: Georgia, 'Times New Roman', Times, serif;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.App-body {
  text-align: center;
  color : #D99729;
  font-family: Georgia, 'Times New Roman', Times, serif;
}

.grid-calculo{
  display: grid;
  justify-content: center;
  min-height: 00vh;
  grid-template-columns: repeat(3, 120px);
  grid-template-rows: minmax(70px, auto) repeat(2, 90px);
}

.grid-calculo > p{
  font-size: 2rem;
  color: #DAA520;
  font-weight: bold;
  margin: 3px;
}

.iniciobtn {
  color: white;
  font-size: calc(15px + 2vmin);
  background: #DAA520;
  font-family: Georgia, 'Times New Roman', Times, serif;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.23);
  border-radius: 10px;
  margin: 50px;
  justify-content: center;
  display: flex;
  padding: 1rem;
  text-align: center;
  align-items: center;
}

.Badge {
    background: #a15d98;;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.10);
    border-radius: 8px 8px 8px 8px;
    overflow: hidden;
  }
  
  .Badge__header {
    padding: 0.5rem 0;
    height: 30px;
    display: flex;
    justify-content: center;
  }
  
  .frmGeneral{
    justify-content: center;
    display: flex;
  }

  .badge_aspectos {
    margin: 3em;
  }

  .form-numero{
    color: white;
    text-align: center;
    background-color: #D99729;;
}

  .Badge__section-dia {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1rem 0;

  }
  
  .Badge__section-anio {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 1rem 0;
    background: #D99729;
  }

  .Badge__section-resultado {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1rem 0;
  }

  .App-logo {
    max-width: 15%;
    height: 15%;
    position: center;
  }

  .navheader{
      width: 100%;
      padding: 0.5rem 0;
      background-color: #a15d98;
  
  }

  .logoName{
    position: absolute;
    align-items: center;
    top: 10%;
    right: 40%;
  }
.fecha_ingresa{
    justify-content: center;
    display: flex;
    padding: 1rem 0;
    background: #D99729;
}

.form-group{
    color: white;
    font-weight: bold;
    font-style: normal;
    margin: 1rem; 
    font-size: 1.5rem;
    text-align: center;
}

.form-control{
    width: 100%;
    padding: 12px;
    margin: 8px 0;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
    resize: vertical;
    text-align: center;
}

.container{
    background-color: #a15d98;
    font-family: serif;
}

h1 {
    color: white;
    text-align: center;
}


  
